nav li {
    display: inline-block;
    padding: 26px 0;
    width: 33.3%;
    text-align: center;
}

nav {
    height: 100px;
    background-color: #00e676;
    color: #fafafa;
}

.logo {
    font-size: 50px;
    padding-top: 10px;
    color: #fafafa;
}

.guessMessage {
    font-size: 30px;
    padding-top: 15px;
}

.scoreSection {
    font-size: 35px;
    padding-top: 15px;
}

@media only screen and (max-width: 600px) {

    nav li {
        display: inline-block;
        padding: 26px 0;
        text-align: center;
    }

    nav {
        height: 70px;
        background-color: #00e676;
        color: #fafafa;
    }

    .logo {
        font-size: 18px;
        padding-top: 10px;
        color: #fafafa;
    }

    .guessMessage {
        font-size: 11px;
        padding-top: 10px;
    }

    .scoreSection {
        font-size: 13px;
        padding-top: 10px;
    }
}

@media only screen and (max-width: 375px) {

    nav li {
        display: inline-block;
        text-align: center;
    }

    nav {
        height: 70px;
        background-color: #00e676;
        color: #fafafa;
    }

    .logo {
        font-size: 13px;
        padding-top: 10px;
        color: #fafafa;
    }

    .guessMessage {
        font-size: 9px;
        padding-top: 10px;
    }

    .scoreSection {
        font-size: 9px;
        padding-top: 10px;
    }
}


