.header-section {
    background-image: url("./img/background-img.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position-y: 90%;
    color: white;
    height: 350px;
    text-align: center;
}

h1 {
    margin: 0;
    padding-top: 50px;
    padding-bottom: 15px;
}

@media only screen and (max-width: 600px) {
    .header-section {
        background-position-x: 50%;
    }
}


